    position: absolute;
    top: 0;
    left: 75px;
  }

  .btn_image {
    width: 150px;
    height: 39px;
    margin: 60px 0 80px 200px;
    cursor: pointer;
    border-radius: 25px;
    text-align: center;
    line-height: 39px;
    color: #788188;
    background-color: #fff;
    border: 1px solid #dbe2e7;
    border-bottom-color: #d5dde3;
    box-shadow: 0 1px 1px rgba(90, 90, 90, 0.1);
  }

  .btn_image:hover {
    border: 1px solid #adadad;
