  }

  .context > .headimg_up {
    margin-left: 210px;
    position: relative;
  }

  .context > .headimg_up > img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    display: block;
  }

  .headimg_up > .zhezhao {
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    top: 0;
    bottom: 0;
    width: 150px;
    border-radius: 50%;
    text-align: center;
    color: #fff;
    opacity: 0;
    cursor: pointer;
  }

  .headimg_up > .zhezhao:hover {
    opacity: 1;
  }

  .headimg_up > .zhezhao > i {
    margin-top: 43px;
    display: block;
  }

  .checkbox_span {
    margin-left: 20px;
  }

  .context > #categroy, .label {
    overflow: hidden;
    margin-left: 200px;
  }

  .context > #categroy > li {
    float: left;
    margin-right: 10px;
    border: 1px solid #bbb;
    padding: 5px 20px;
    margin-bottom: 10px;
    cursor: pointer;
  }

  .context > .label > .label_new {
    cursor: pointer;
    color: #4cb6cb;
    border: 1px solid #4cb6cb;
  }

  .context > .label > .choose {
    cursor: pointer;
  }

  .context > .label > .choose:hover {
    background: #bbb;
    color: #fff;
  }

  .context > .label > .label_new:hover {
    box-shadow: 0 0 2px 0 #4cb6cb;
  }

  .context > .label > li {
    float: left;
    margin-right: 10px;
    border: 1px solid #bbb;
    padding: 5px 20px;
    margin-bottom: 10px;
    min-width: 72px;
    min-height: 42px;
    box-sizing: border-box;
    text-align: center;
    position: relative;
  }

  .newlabel {
    position: absolute;
    top: 0;
    width: 100%;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
  }

  .newlabel:hover {
    opacity: 1;
  }

  .context > #categroy > li:hover {
    background: #bbb;
    color: #fff;
  }

  .categroy_active {
    background: #bbb;
    color: #fff;
  }

  #project .deleteproject {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
  }

  #project .deleteproject:hover, .deleteproject > span {
    opacity: 1;
    display: block !important;
  }

  #project .deleteproject > span {
    display: none;
    float: right;
    width: 70px;
    height: 24px;
    border: 1px solid #bbb;
    cursor: pointer;
    line-height: 24px;
    text-align: center;
  }

  .mymodal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 20;
  }

  .mymodal > .conte {
    width: 630px;
    margin: 30px auto;
    overflow: hidden;
    background: #FFF;
    border-radius: 5px;
    padding: 15px;
  }

  #img-cont {
    width: 400px;
    height: 400px;
    float: left
  }

  #img-pre {
    width: 160px;
    height: 120px;
    overflow: hidden;
  }

  .mymodal > .conte > .div_btns {
    width: 100%;
    height: 50px;
    float: left;
    line-height: 50px;
  }

  .mymodal > .conte > .div_btns > button {
    border-radius: 25px;
    width: 70px;
    height: 30px;
    outline: none;
    cursor: pointer;
    text-align: center;
    color: #788188;
    background-color: #fff;
    border: 1px solid #dbe2e7;
    border-bottom-color: #d5dde3;
    box-shadow: 0 1px 1px rgba(90, 90, 90, 0.1);
  }

  .mymodal > .conte > .div_btns > button:hover {
    border: 1px solid #adadad;
    color: #333;
    background-color: #ebebeb;
  }

  .mymodal > .conte > .div_btns > i {
    display: inline-block;
    width: 50px;
    text-align: center;
    vertical-align: top;
    cursor: pointer;
  }
</style>



